
html, body {
  overflow-x:hidden;
  background-color:white;
  margin:0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

header, footer {
  float:left;
  background:white;
  width:100vw;
}

header .container {
  padding:0px;
}

h2 {
  text-align:center;
  color:rgba(94, 41, 100, 1);
}

nav {
  float:left;
  margin-top:16px;
}

.logo{
  float:left;
  padding-left:15px;
}

.NDIS-logo {
  float:left;
  display: block;
}

.NDIS-logo.top{
  float:right;
  padding-bottom:0px;
  margin-top:-5px;
}

.container {
  max-width:1200px;
  margin:auto;
  padding:10px 20px;
  height:100%;
}

.logo img {
  height:70px;
  padding-top:5px;
}

.nav-links {
  display:flex;
  opacity:0;
  gap:20px;
}

.nav-links.show {
  opacity:1;
  transform:scale(1);
  pointer-events: auto; /* Enable clicks when shown */
}

.nav-links.mobile {
  background-color:#9c9c9c;
  gap:0px;
  padding-left:0px;
  transform: scale(1);
  pointer-events:none; /* Prevent clicks when hidden */
  transition: opacity 0.9s ease, transform 0.9s ease;
}

.mobile-socials {
  display:none!important;
}

.mobile-socials.show {
  display:flex!important;
  justify-content: left; /* Centers items horizontally */
  align-items: left; 
  background-color:rgba(94, 41, 100, 1)!important;
  padding-left:35px!important;
}

.nav-links.mobile li {
  padding-left:30px;
}

.nav-links.mobile li:nth-child(odd) {
  background-color:#9c9c9c;
}

.nav-links.mobile li:nth-child(even) {
  background-color:#8c8c8c;
}

.nav-links.mobile a {
  color:white;
  padding:10px;
  float:left;
}

.nav-links.mobile a:hover {
  color:white;
}

.nav-links.mobile li:hover {
  cursor:pointer;
}

.nav-links li {
  display: inline;
}

.nav-links a {
  text-decoration:none;
  color:#333;
}

.nav-links a:hover {
  text-decoration:none;
  color:rgba(94, 41, 100, 1);
}

.social-links {
  display:block;
}

.social-links a {
  margin:0 10px;
  text-decoration:none;
  color:#4caf50;
}

.menu-toggle {
  display:none;
  margin-top:10px;
  color:rgba(94, 41, 100, 1);
}

.logo.mobile {
  display:none;
}

footer {
  padding-bottom:7px;
}

footer .NDIS-logo {
  padding-top:11px;
}

footer .App-logo {
  padding-top:6px;
}

footer .container {
  padding:0px 20px;
}


/* Responsive */
@media (max-width: 468px) {

  .logo.mobile {
    display:block;
    padding-left:15px;
  }

  .logo.desktop {
    display:none;
  }

}



/* Responsive */
@media (max-width: 568px) {

  .blurb{
  display:none;
  }

  .blurb-mobile{
  display:block!important;
  font-size:0.9em;
  line-height:1.2em!important;
  }

  #about {
    padding-bottom:30px;
  }

}



/* Responsive */
@media (max-width: 768px) {

  .hero .service-cards {
    width:100%;
  }

  .hero .container {
    padding-bottom:50px;
  }

  .blurb {
    padding:2%!important;
  }

  .nav-links {
    flex-direction:column;
    gap:10px;
  }

  nav {
    margin-top:0px!important;
    height:0px;
  }

  nav ul {
    margin:0px!important;
  }
 
  .nav-links.mobile {
    width:100vw;
    pointer-events: all;
  }

  .nav-links.active {
    display:flex;
  }

  .menu-toggle {
    display:block;
    background:none;
    border:none;
    font-size:20px;
    cursor:pointer;
    float:right;
    margin-top:25px;
    margin-right:20px;
  }

  .NDIS-logo {
    display: none;
  }

  .social-media {
    display: none;
  } 

  .contact {
    width: 100%!important;
    padding-left:0px!important;
    padding-right:0px!important;
  }

  .contact .container {
    padding-left:17px!important;
    padding-right:17px!important;
  }

}

@media (max-width: 900px) {
  .NDIS-logo {
    display: none;
  }
}

@media (max-width: 800px) {
  .social-media {
    display: none;
  } 
}

.footer-cw{
  color:#909090;
  font-size:0.7em;
  margin-top: -21px;
}

.footer-logo{
  max-height:55px;
  height:12vmin!important;
