.contact-form {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.form-input, .form-textarea {
  padding:12px 15px;
  border:1px solid #ccc;
  border-radius:8px;
  font-size:1rem;
  font-family:'Arial', sans-serif;
  outline:none;
  transition:border-color 0.3s, box-shadow 0.3s;
}

.form-input:focus, .form-textarea:focus {
  border-color: rgba(130,188,230,0.7);
  box-shadow: 0 0 8px rgba(0, 115, 230, 0.3);
}

.error:focus {
  border-color: red;
}

.form-textarea {
  min-height: 120px;
  resize: none;
}

.form-button {
  background: rgba(94, 41, 100, 1);
  color: #fff;
  font-size: 1.1rem;
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.form-button:hover {
  background: rgba(84, 31, 90, 1);
  box-shadow: 0 4px 12px rgba(0, 115, 230, 0.3);
}

/* Responsive Design */
@media (max-width: 768px) {
