.map{
  height:50vh;
  overflow:hidden;
}

.map .container {
  width:100%;
  padding:0px;
  margin:0px;
  max-width:100%;
}

#Gallery h2{
  margin-top: 40px;
  text-align: left;
  padding-left: calc(10% + 10px + 1rem);
  float:left;
}

.gallery-title {
  text-align: center;
  margin-top: 1rem;
  font-family: Arial, sans-serif;
}

.gallery-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  padding: 1rem;
  border-radius:6px;
  width:80%;
  padding:0px;
  padding-left:10%;
  padding-bottom:50px;
  padding-top:10px;
}

.gallery-thumb {
  object-fit: cover;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  transition: transform 0.2s ease;
  border-radius:5px;
  flex: 1 1 calc(12.5% - 1rem);
  max-width: calc(12.5% - 1rem);
}

@media (max-width: 868px){
  .gallery-thumb {
    flex: 1 1 calc(33.333333% - 1rem);
    max-width: calc(33.333333% - 1rem);
  }
  .gallery-thumb-7, .gallery-thumb-8 {
    display: none;
  }
  #Gallery h2 {
    text-align:center!important;
    float:left!important;
    padding-left: calc(7% + 10px + 1rem);
  }
}

.gallery-thumb:hover {
  transform: scale(1.05);
  border-color: rgba(130,188,230,0.7);
  box-shadow: 0 0 8px rgba(0, 115, 230, 0.3);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 1000;
  user-select: none;
}

.modal-image {
  max-width: 80%;
  max-height: 80%;
  border-radius: 6px;
  cursor: auto;
  box-shadow: 0 0 15px rgba(255,255,255,0.5);
  user-select: none; /* Prevent text selection */
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  pointer-events:auto;
  cursor:grab;
}

.arrow:hover {
  color: #333;
}

.left-arrow {
  left: 2%;
}

.right-arrow {
  right: 2%;
}

@media (max-width: 568px) {

  .left-arrow, .right-arrow {
    position:absolute;
    bottom:50px;
  }

  .left-arrow {
    left:0;
  }

  .right-arrow {
    right:0;
  }



}

.hero .service-cards {
  gap:0px;
