}

.testimonial-slider {
  max-width: 600px;
  margin: auto;
  text-align: center;
  position: relative;
}

.slide {
  margin: 20px 0;
}

.text {
  font-size: 1rem;
  font-style: italic;
  color: #333;
}

.author {
  font-size: 1rem;
  color: #777;
  margin-top: 10px;
}

.controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.arrow {
border: none;
font-size: 0.8rem;
cursor: pointer;
color: #000000;
padding: 10px;
margin: 40px;
border-radius: 20px;
padding-left: 14px;
padding-right: 14px;
}

.close-arrow {
  position:absolute;
  right:0;
  top:0;
  font-weight:500;
}

.testimonials .arrow {
  background-color:#333;
  color:white;
  display:none;
}

.dots {
  display: flex;
  gap: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ccc;
  cursor: pointer;
}

.dot.active {
  background-color: #333;
}

