  .hero h1 {
    font-size:4em;
  }
  p {
    font-size:1.2rem!important;
  }
  p.footer-cw {
    font-size:1rem!important;
  }
  .nav-links a {
    font-size:1.1rem!important;
  }
  .special-section {
    flex:4 1!important;
  }
  .hero .title-card {
    transform: translateX(-60%);
  }
}
@media screen and (min-width: 2300px) {
  .hero .title-card {
    transform: translateX(-65%);
  }
  .hero h1 {
    font-size:4.5em;
  }
}

@media screen and (max-width: 409px) {
  #email_live {
    flex-basis:100%;
    margin-left:-5px;
  }
}
